@import '../styles/utils.scss';

.info-container {
  width: auto;
  margin: 64px 24px 164px 24px;

  .info-logo {
    margin-bottom: 32px;

    cursor: pointer;
  }

  .info-body {
    font-size: 17px;
    font-weight: 700;
    line-height: 26px;
  }

  .icon-container {
    display: flex;
    justify-content: space-between;

    margin-top: 49px;

    width: 121px;

    .info-icon {
      margin-right: 20px;

      cursor: pointer;
    }
  }
}

@media only screen and (min-width: 376px) {
  .info-container {
    padding-bottom: 100px;

    .info-logo {
      margin-bottom: 38px;
    }

    .info-body {
      width: 390px;

      font-size: 15px;
    }
  }
}
