.brand-container {
  display: flex;
  width: 100%;

  .background {
    position: relative;

    right: -418px;
    top: -90px;

    width: 226px;
  }

  .brand-logo {
    position: relative;

    top: 40px;
    right: -24px;

    cursor: pointer;
  }
}

@media only screen and (min-width: 376px) {
  .brand-container {
    overflow: hidden;

    .brand-logo {
      top: 62px;
      right: -39px;
    }
  }
}

@media only screen and (max-width: 376px) {
  .background {
    display: none;
  }
}
