.overview-container {
  width: auto;
  margin: 0 24px;

  .mobile-heading {
    margin-bottom: 20px;
  }

  .mobile-body {
    margin-bottom: 68px;
  }

  @media only screen and (min-width: 376px) {
    margin-left: 39px;

    .mobile-heading {
      font-size: 64px;
      letter-spacing: -0.73px;
      line-height: 64px;

      width: 520px;
      margin-top: 12px;
      margin-bottom: 28px;
    }

    .mobile-body {
      width: 514px;

      font-size: 18px;
      line-height: 28px;
    }
  }
}
