@import './utils.scss';

.demo-container {
  display: flex;
  padding-top: 98px;

  .demo-card {
    display: flex;
    justify-content: center;

    border-radius: 12px;

    width: 100%;

    background-color: $black;

    .illustration {
      position: absolute;
      top: 524px;
    }

    @media only screen and (max-width: 375px) {
      .illustration {
        width: 209px;
      }
    }

    .demo-background {
      width: 280px;
      height: 420px;
    }
  }

  .pricing-card {
    align-self: flex-end;
    position: absolute;

    border-radius: 12px;
    padding: 48px 36px;
    height: 546px;
    width: 100%;

    background-color: $tomato;

    .pricing-heading {
      margin-bottom: 12px;
    }

    .pricing-value {
      display: flex;
      align-items: center;

      margin: 8px 0 32px 0;
      height: 53px;

      font-size: 18px;

      color: $white;

      .pricing-span {
        margin-right: 15px;

        font-size: 65px;
      }
    }

    .ios-button {
      display: flex;
      justify-content: center;
      align-items: center;

      width: 296px;
      height: 61px;
      border-radius: 12px;
      border: none;
      margin-bottom: 16px;
      margin-inline: auto;

      font-size: 18px;
      font-weight: 700;
      word-spacing: -0.18px;

      color: $white;
      background-color: $black;

      cursor: pointer;
    }

    .ios-button:hover {
      background-color: $cyan;
    }

    .android-button {
      display: flex;
      justify-content: center;
      align-items: center;

      width: 296px;
      height: 61px;
      border-radius: 12px;
      border: none;
      margin-inline: auto;

      font-size: 18px;
      font-weight: 700;
      word-spacing: -0.18px;

      color: $black;
      background-color: $white;

      cursor: pointer;
    }

    .android-button:hover {
      background-color: $mustard;
    }

    .icon {
      margin-right: 8px;
    }
  }
}

@media only screen and (min-width: 376px) {
  .demo-container {
    margin-top: 111px;

    .illustration {
      left: 81px;
      margin-top: 73px;

      width: 265px;

      z-index: 1;
    }

    .demo-background {
      position: relative;
      top: -44px;
    }

    .demo-card {
      width: 696px;
      height: 600px;
      margin-inline: 33px;
    }

    .pricing-card {
      right: 97px;
      height: 546px;
      width: 417px;
      margin-bottom: 102px;

      z-index: 2;
    }
  }
}
